import { Basic, Action, ActionTypes } from "../actions";

// default state
const defaultState = {
  loading: true,
  domains: [],
  priorities: [],
  roles: [],
} as Basic;

export const basicReducer = (state: Basic = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.LOGOUT:
      return defaultState;
    case ActionTypes.ADD_BASIC_INFO:
      return {
        ...state,
        domains: action.payload.domains,
        loading: false,
        priorities: action.payload.priorities,
        roles: action.payload.roles,
      } as Basic;
    default:
      return state;
  }
};
