import axios from "axios";
import { Dispatch } from "redux";
import { APP_URL } from "../config/app.config";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";
import { ActionTypes } from "./types";

// action url
const url = APP_URL;

/**
 * * ****************************** INTERFACES *****************************
 */

export interface BasicRoles {
  role_id: string;
  role_name: string;
  access: string;
  status: string;
  constant: string;
}

export interface BasicDomain {
  domain_id: string;
  domain_name: string;
  status: string;
}

export interface BasicPriority {
  id: string;
  title: string;
  security_check: boolean;
}

export interface Basic {
  loading: boolean;
  roles: BasicRoles[];
  domains: BasicDomain[];
  priorities: BasicPriority[];
}

export interface Basic_API {
  roles: BasicRoles[];
  domains: BasicDomain[];
  priorities: BasicPriority[];
}

/**
 * * ****************************** ACTIONS *****************************
 */
export interface AddBasicInfo {
  type: ActionTypes.ADD_BASIC_INFO;
  payload: Basic_API;
}

export type FT_AddBasicInfo = (
  callBack: (status: boolean, msg: string) => void
) => void;
export const FC_AddBasicInfo = (
  callBack: (status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<Basic_API>(`${url}/base`);
      // * save data
      dispatch<AddBasicInfo>({
        type: ActionTypes.ADD_BASIC_INFO,
        payload: res.data,
      });
      callBack(true, "");
    } catch (error) {
      callBack(
        false,
        errorToText(error, () => {})
      );
    }
  };
};
