import axios from "axios";
// import { Dispatch } from "redux";
import { APP_URL } from "../config/app.config";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";
// import { ActionTypes } from "./types";

// action url
const url = `${APP_URL}/document`;

/**
 * * ****************************** INTERFACES *****************************
 */

export interface DocumentsListInterface {
  closed: number;
  status: number;
  document_id: string;
  document_code: string;
  document_url: string;
  domain_id: string | null;
  priority_id: string | null;
  document_reference_code: string | null;
  registered_by_id: string;
  registered_date: Date;
  owner_name: string | null;
  owner_institute: string | null;
  owner_phone: string | null;
  owner_email: string | null;
}

export const FC_GetDocuments = async (
  enum_id: string,
  domain_id: string,
  callBack: (
    status: boolean,
    data: DocumentsListInterface[],
    msg: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.get<DocumentsListInterface[]>(
      `${url}/${enum_id}/${domain_id}`
    );

    callBack(true, res.data, "");
  } catch (error) {
    callBack(false, [], errorToText(error));
  }
};

