export interface DocumentDetails_API {
  document: DocumentInterface;
  users: {
    minister: DocumentUserInterface | null;
    cecat: DocumentUserInterface | null;
    director: DocumentUserInterface | null;
    govenor: DocumentUserInterface | null;
    expert: DocumentUserInterface | null;
    expeditor: DocumentUserInterface | null;
    registered_by: DocumentUserInterface | null;
  };
  reference_document: ReferenceDocument | null;
}

export const FC_GetDocumentDetails = async (
  document_id: string,
  callBack: (
    status: boolean,
    data: DocumentDetails_API | null,
    msg: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.get<DocumentDetails_API>(
      `${url}/details/${document_id}`
    );

    callBack(true, res.data, "");
  } catch (error) {
    callBack(false, null, errorToText(error));
  }
};

export const FC_UpdateDocument = async (
  data: {
    document_id: string;
    // [itm: string]: string | null | Date | any[];
    [itm: string]: any;
  },
  callBack: (
    status: boolean,
    data: DocumentInterface | null,
    msg: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.put<{ document: DocumentInterface; msg: string }>(
      `${url}/`,
      data
    );

    callBack(true, res.data.document, "");
  } catch (error) {
    callBack(false, null, errorToText(error));
  }
};

