export enum UsersAccess {
  DOCUMENTS = "DOCUMENTS",
}

export enum UserRoles {
  EXPERT = "EXPERT",
  GOVENEUR = "GOVENEUR",
  SOUS_GOVENER = "SOUS_GOVENER",
  MINISTOR = "MINISTOR",
  CECAT = "CECAT",
  EXPEDITION = "EXPEDITION",
  SECRETARY = "SECRETARY",
  CABINET_OFFICE = "CABINET_OFFICE",
  CONSEILLER = "CONSEILLER",
  DIRECTOR = "DIRECTOR",
}

export const checkAccess = (
  role: string | null | undefined,
  userAccess: UsersAccess
) => {
  if (role === undefined || role === null) {
    return false;
  } else {
    return role.indexOf(userAccess) >= 0 ? true : false;
  }
};

export const RenderPageAccess = (
  role: string | null | undefined,
  userAccess: UsersAccess,
  callBack: () => void
) => {
  if (!checkAccess(role, userAccess)) {
    window.location.href = "/dashboard";
    return;
  } else {
    callBack();
  }
};
